<template>
    <Layout>
        <template v-slot:header>
            <!-- //* header -->
            <div class="mine_header clearfix">
                <div class="mine_header-head">
                    <i class="iconfont icon-theme"></i>
                    <i class="iconfont icon-email"></i>
                    <i class="iconfont icon-setting"></i>
                </div>
                <div class="mine_header-info">
                    <label class="mine_header-info-image">
                        <img :src="avatar" alt="">
                        <input type="file" id="avatar_loader" style="display: none;" @change="avatarLoaded">
                    </label>
                    <div class="mine_header-info-details">
                        <a href="/login" v-if="!userPhone">登录</a>
                        <a href="/user-info">个人信息<i class="iconfont icon-next-m"></i></a>
                    </div>
                </div>
            </div>
        </template>
        <!-- //* mine -->
        <div class="mine">
            <div  v-if="userPhone">
                <ul class="mine-list">
                    <router-link class="mine-list-item"
                        tag="li"
                        :to="item.link"
                        v-for="(item, index) in mines" :key="index"
                    >
                        <i class="iconfont"
                            :class="item.icon"
                        ></i>
                        <p class="mine-list-item-label">{{item.label}}</p>
                    </router-link>
                </ul>
            </div>
            <NotLogin v-else/>
        </div>
    </Layout>
</template>

<script>
import Layout from '../layout/Layout.vue'
import NotLogin from '@/components/base/NotLogin.vue'
import { mapState } from 'vuex'
import axios from 'axios'

export default {
    name: 'Mine',
    components: {
        Layout,
        NotLogin
    },
    data() { 
        return {
            mines: [
                {
                    icon: 'icon-star',
                    label: '我的收藏',
                    link: '/mine/myfavorite'
                },
                {
                    icon: 'icon-foot',
                    label: '我的足迹',
                    link: '/mine/foot'
                },
                {
                    icon: 'icon-1301dizhi',
                    label: '收货地址',
                    link: '/mine/address'
                },
                {
                    icon: 'icon-i-money',
                    label: '余额',
                    link: '/mine/balance'
                },
                {
                    icon: 'icon-about',
                    label: '关于',
                    link: '/mine/about'
                },
            ],
            avatar: 'http://dummyimage.com/75x75',
        }
    },
    computed: {
        ...mapState('login', {
            userPhone: state => state.userPhone
        })
    },
    methods: {
        avatarLoaded(){
            var file = document.getElementById("avatar_loader").files[0]
            var avatar = new FormData()// 创建form对象
            avatar.append('avatar',file)
            let config = {
                headers:{'Content-Type':'multipart/form-data'}, //添加请求头
                responseType: 'blob' //指定返回数据的格式为blob
            }
            axios.post('/api/load-avatar', avatar, config)
                .then(res => {
                    this.blobToBase64(res.data)
                        .then(res => {
                            // res 为 base64
                            this.avatar = res
                        })
                })
        },
        // 二进制转换成 base64
        blobToBase64(blob) {
            return new Promise((resolve, reject) => {
                const fileReader = new FileReader()
                fileReader.onload = (e) => {
                    resolve(e.target.result)
                }
                // readAsDataURL
                fileReader.readAsDataURL(blob)
                fileReader.onerror = () => {
                    reject(new Error('文件流异常'))
                }
            })
        }
    },
}
</script>

<style lang="less">
@import "~@/assets/css/mixin.less";

.test {
    width: 100px;
    height: 100px;
}
.mine_header{
    color: #fff;
    background-color: #00c0ad;
    .padding-horizontal(@padding-base);
    &-head{
        .padding-vertical(8px);
        text-align: right;
        &>.iconfont{
            margin-left: 10px;
            .font-size-rem(18px);
        }
        color: #fff;
    }
    &-info{
        .flex-sb-center();
        .padding-vertical(@padding-base);
        &-image{
            .flex-shrink(0);
            .image-wrapper(75px);
        }
        &-details{
            height: 75px;
            flex-grow: 1;
            .margin-left-rem(@margin-base);
            .flex-column();
            .justify-content(space-around);
        }
    }
}
.mine{
    height: 100%;
    &-list{
        .margin-top-rem(@margin-base);
        .padding-horizontal(@padding-base);
        background-color: @bg-white;
        &-item{
            .padding-horizontal(@padding-sm);
            .flex-sb-center();
            border-top: 1px solid #e3e3e3;
            &:first-of-type{
                border-top: 0;
            }
            // 图标
            &>.iconfont{
                flex-shrink: 0;
                color: @color-primary;
                .font-size-rem(20px);
            }
            &-label{
                .margin-left-rem(@margin-base);
                flex-grow: 1;
                line-height: 45px;
            }
        }
    }
}
</style>